import React from "react";
import { css, styled } from "styled-components";
import { Link } from "react-router-dom";
import LoginPage from "./LoginPage";
import { useDispatch, useSelector } from "react-redux";
import { getAuth, signOut } from "firebase/auth";
import { logoutUser } from "../store/auth";

const Nav = () => {
  const user = useSelector(state => state.auth.user);
  const dispatch = useDispatch();
  const handleLogout = () => {
    const auth = getAuth();
    signOut(auth).then(()=> {
      dispatch(logoutUser());
      alert('.');
      localStorage.removeItem('user')
    }).catch((error) => {
      console.error('   :' ,error);
    })
  }

  return( 
    <MenuBar>
      <Menu>
        <Link to='/'>
          <h1>logo</h1>
        </Link>
        <Link to='/search'>
          <Search></Search>
        </Link>
        {user ? (
          <LoggedInUser onClick={handleLogout}>{user.email}</LoggedInUser>
        ) : (
          <Link to='/login'>
            <Login></Login>
          </Link>
        )}
      </Menu>
    </MenuBar>
  )
};

export default Nav

const theSame = css`
  font-weight: 700;
  cursor: pointer;
  color: white;
`

const MenuBar = styled.nav`
  width: 100%;
  height: 60px;
  background-color: #292727;
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 99;
  flex-wrap: wrap;
`
const Menu = styled.div`
  width: 200px;
  margin-left: auto;
  margin-right: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const Search = styled.div`
  ${theSame}
`
const Login = styled.div`
  ${theSame}
`

const LoggedInUser = styled.div`
  ${theSame}
`